<template>
    <div class="contain">
      <h1>永久删除账号</h1>
      <ul>
        <li>如果你不小心创建出了多余的帐号，可以通过此功能删除账号。</li>
        <li>账号删除前请自动做好清理工作。</li>
        <li>删除帐号是不可逆的操作，删除后将无法恢复。</li>
      </ul>
      <el-button type="success" plain>保留账号</el-button>
      <el-button type="danger" plain @click="deleteUser">删除账号</el-button>
    </div>
</template>

<script>
    export default {
      name: "deleteUser",
      computed:{
        getCurUser(){
          return this.$store.getters.getCurUser
        }
      },
      methods: {
        deleteUser(){
          this.$axios.get('/blog/api/deleteUser?id='+this.getCurUser.id).then((res) => {
            if(!res.data){
              this.$message({
                showClose: true,
                message: '删除失败',
                type: 'error'
              })
              return
            }else {
              this.$message({
                showClose: true,
                message: '删除成功',
                type: 'success'
              })
              this.$store.dispatch('userLogout')
              localStorage.setItem("userLogin",false)
              localStorage.removeItem("cur_user")
              this.$router.push('/login')
            }
          })
        }
      }
    }
</script>

<style scoped>
  .contain{
    color: #333333;
  }
  .contain h1{
    font-size: 20px;
  }
  .contain ul{
    margin-top: 20px;
    list-style: disc;
  }

  .contain ul li{
    margin-bottom: 20px;
  }
</style>
